<template >
  <v-content relative>
    <v-container>
      <v-row justify="center">
        <v-col cols="12" sm="8" md="4">
          <v-card-text class="vcard" text-xs-center>
            <v-form @submit.prevent="login">
              <p class="display-1 text-center font-weight-bold">반반한 동네</p>
              <p class="text-center">사장님 페이지</p>
              <br />
              <v-row>
                <v-text-field
                  v-model="id"
                  id="id"
                  :counter="10"
                  height="3em"
                  label="아이디"
                  type="text"
                  outlined
                  dense
                  required
                ></v-text-field>
              </v-row>
              <v-row>
                <v-text-field
                  v-model="pw"
                  id="pw"
                  height="3em"
                  label="비밀번호"
                  outlined
                  dense
                  type="password"
                  @keyup.enter="Slogin()"
                ></v-text-field>
              </v-row>
              <p class="errorMessage">{{ error }}</p>
              <v-row fluid>
                <a>아이디</a>/
                <a>비밀번호 찾기</a>
                <router-link :to="{ name: 'SsignUp'}" style="margin-left: 20px;">회원가입</router-link>
              </v-row>
              <br />
              <v-row>
                <v-btn
                  large
                  color="primary"
                  @click="Slogin()"
                  width="100%"
                  height="50"
                  fill="#0076ff"
                  font-size="16"
                  font-family="SourceHanSansK-Bold, Source Han Sans K"
                  font-weight="700"
                >로그인</v-btn>
              </v-row>
            </v-form>
          </v-card-text>
        </v-col>
      </v-row>
    </v-container>
  </v-content>
</template>

<script>
import Kakao from "../../Kakao.js";
import KakaoLogin from "@/components/KakaoLogin.vue";
import GoogleLogin from "@/components/GoogleLogin.vue";

export default {
  components: {
    KakaoLogin,
    GoogleLogin
  },
  data() {
    return {
      id: "",
      pw: "",
      name: 123,
      tel: 123,
      email: 123,
      address: 123,
      chk: 0,
      point: 0,
      signupDate: 214,
      likes: 0,
      error: null
    };
  },
  methods: {
    Slogin() {
      this.$store
        .dispatch("Slogin", {
          id: this.id,
          pw: this.pw,
          name: this.name,
          tel: this.tel,
          email: this.email,
          address: this.address,
          chk: this.chk,
          point: this.point,
          signup_date: this.signupDate,
          likes: this.likes,
          longitude: 0,
          latitude: 0,
          category: 0
        })
        .then(() => {
          this.$router.push({ name: "StoreMainPage" });
        })
        .catch(err => {
          if (err.response == undefined) {
            this.error = "* 가입하지 않은 아이디이거나, 잘못된 비밀번호입니다.";
          }
        });
    }
  }
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Nanum+Gothic&display=swap");
@import url("https://fonts.googleapis.com/css?family=Black+Han+Sans|Do+Hyeon|Jua|Nanum+Gothic|Sunflower:300");
* {
  font-family: "Do Hyeon", sans-serif;
}
.social {
  margin-right: 20px;
}
.errorMessage {
  text-align: center;
  color: red;
}
</style>